export default {
	props: {
		// 
		title: {
			type: String,
			default: ''
		},
		// 
		showTitle: {
			type: Boolean,
			default: true
		},
		// 
		showSubtitle: {
			type: Boolean,
			default: true
		},
		// single-multiple-range-
		mode: {
			type: String,
			default: 'single'
		},
		// mode=range
		startText: {
			type: String,
			default: ''
		},
		// mode=range
		endText: {
			type: String,
			default: ''
		},
		// 
		customList: {
			type: Array,
			default: () => []
		},
		// 
		color: {
			type: String,
			default: '#3c9cff'
		},
		// 
		minDate: {
			type: [String, Number],
			default: 0
		},
		// 
		maxDate: {
			type: [String, Number],
			default: 0
		},
		// modemultiplerange
		defaultDate: {
			type: [Array, String, Date, null],
			default: null
		},
		// mode=multiple
		maxCount: {
			type: [String, Number],
			default: Number.MAX_SAFE_INTEGER
		},
		// 
		rowHeight: {
			type: [String, Number],
			default: 56
		},
		// 
		formatter: {
			type: [Function, null],
			default: null
		},
		// 
		showLunar: {
			type: Boolean,
			default: false
		},
		// 
		showMark: {
			type: Boolean,
			default: true
		},
		// 
		confirmText: {
			type: String,
			default: ''
		},
		// 
		confirmDisabledText: {
			type: String,
			default: ''
		},
		// 
		closeOnClickOverlay: {
			type: Boolean,
			default: false
		},
		// 
		closeOnClickConfirm: {
			type: Boolean,
			default: true
		},
		// 
		readonly: {
			type: Boolean,
			default: false
		},
		// 	
		showConfirm: {
			type: Boolean,
			default: true
		},
		// mode = range Infinity
		maxRange: {
			type: [Number, String],
			default: Number.MAX_SAFE_INTEGER
		},
		// mode = range
		rangePrompt: {
			type: String,
			default: ''
		},
		// mode = range
		showRangePrompt: {
			type: Boolean,
			default: true
		},
		// mode = range
		allowSameDay: {
			type: Boolean,
			default: false
		},
		// 
		round: {
			type: [Boolean, String, Number],
			default: 0
		},
		// 
		monthNum: {
			type: [Number, String],
			default: 3
		},
		...uni.$uv?.props?.calendar
	}
}