import {
		guid
	} from '@/uni_modules/uv-ui-tools/libs/function/index.js'
class Base {
	constructor({
		id = guid(),
		title = '',
		startDateTime = '',
		endDateTime = '',
		price = null,
		other = ''
	} = {}) {
		Object.assign(this, {id, title, startDateTime, endDateTime, price, other});
	}
}

export class Traffic extends Base {
	constructor(
		outset = '', // 
		destination = '', // 
		order = '', // 
		planB = false, // 
		...baseOptions
	) {
		//  Base 
		super(...baseOptions);
		Object.assign(this, { outset, destination, order, planB });
	}
}

export class Hotel extends Base {
	constructor(
	address = '', // 
	planB = false, // 
	...baseOptions
	) {
		//  Base 
		super(...baseOptions);
		Object.assign(this, { address, planB });
	}
}

export class Activity extends Base {
	constructor(
		eventType = '', //  place / eat / wish
		address = '', // 
		...baseOptions
	) {
		//  Base 
		super(...baseOptions);
		Object.assign(this, { eventType, address });
	}
}