export default {
	props: {
		// 
		show: {
			type: Boolean,
			default: false
		},
		// v-forindex
		name: {
			type: [String, Number],
			default: ''
		},
		// 
		disabled: {
			type: Boolean,
			default: false
		},
		// swipe
		autoClose: {
			type: Boolean,
			default: true
		},
		// 
		threshold: {
			type: Number,
			default: 20
		},
		// 
		options: {
			type: Array,
			default: () => []
		},
		// ms
		duration: {
			type: [String, Number],
			default: 300
		},
		...uni.$uv?.props?.swipeActionItem
	}
}